<template>
  <view class="container">
    <view class="login-title"> 登录账号 </view>
    <view class="login-describe"> 登录后可使用更多的功能 </view>
    <view class="input-content">
      <view class="input-item">
        <input
          type="text"
          placeholder="账号或手机号码"
          id="phone"
          @input="phoneInput"
        />
      </view>
      <view class="input-item">
        <input
          :type="type"
          placeholder="密码"
          id="password"
          @input="passwordInput"
        />
        <image class="look" :src="lookurl" @tap="looks"></image>
      </view>
    </view>
    <button class="confirm-btn" @tap="login">登录</button>
    <view class="forget-section"> 忘记密码? </view>
    <!-- 		<view class="register-section">
	    还没有账号?
	    <text >马上注册</text>
		</view> -->
  </view>
</template>

<script>
import accountInfo from "./loginData.js";
export default {
  data() {
    return {
      phone: "",
      password: "",
      type: "password",
      look: "false",
      lookurl: "../../static/image/login/y1.png",
    };
  },
  onLoad() {
    this.looks();
  },
  onNavigationBarButtonTap(e) {
    if (e.index == 0) {
      uni.navigateTo({
        url: "../register/register",
      });
    }
  },
  methods: {
    phoneInput(event) {
      this.phone = event.target.value;
      // console.log(this.phone);
    },
    passwordInput(event) {
      this.password = event.target.value;
      // console.log(this.password);
    },
    login() {
      let { phone, password } = this.$data;
      // console.log(phone,password);
      if (!phone) {
        uni.showToast({
          title: "手机号不能为空",
          icon: "none",
        });
        return;
      }
      let phoneReg = /^1(3|4|5|6|7|8|9|)\d{9}$/;
      if (!phoneReg.test(phone)) {
        wx.showToast({
          title: "手机号格式有误",
          icon: "none",
        });
        return;
      }
      if (!password) {
        wx.showToast({
          title: "密码不能为空",
          icon: "none",
        });
        return;
      } else {
        uni.redirectTo({
          url: "../chat/chat",
        });
      }
    },
    looks() {
      if (this.look) {
        this.type = "password";
        this.look = !this.look;
        this.lookurl = "../../static/image/login/y1.png";
      } else {
        this.type = "text";
        this.look = !this.look;
        this.lookurl = "../../static/image/login/y2.png";
      }
    },
  },
};
</script>

<style>
@import url("login.css");
</style>